import React from "react";
import styled, { css } from "styled-components";

const StyledText = styled.p`
  ${({ fontSize }) =>
    fontSize &&
    css`
      font-size: ${fontSize};
    `}
  ${({ margin }) =>
    margin &&
    css`
      margin: ${margin};
    `}
    ${({ marginLeft }) =>
    marginLeft &&
    css`
      margin-left: ${marginLeft};
    `}
`;

function AppHeading({
  children,
  fontSize = "24px",
  fontWeight = "500",
  margin = "0",
  marginLeft,
  className,
}) {
  return (
    <StyledText
      fontSize={fontSize}
      fontWeight={fontWeight}
      margin={margin}
      marginLeft={marginLeft}
      className={className}
    >
      {children}
    </StyledText>
  );
}

export default AppHeading;
