import React from "react";
import styled from "styled-components";

import BudgetReportItem from "./BudgetReportItem";

const ItemListContainer = styled.div`
  padding-left: 15px;
  padding-right: 15px;
`;

function BudgetReportItemListContainer({ array }) {
  const listItems = array.map((item) => (
    <BudgetReportItem
      key={item.label}
      category={item.category}
      label={item.label}
      amount={item.amount}
      color={item.color}
    />
  ));
  return (
    <ItemListContainer>
      <ul style={{ margin: 0, padding: 0 }}>{listItems}</ul>
    </ItemListContainer>
  );
}

export default BudgetReportItemListContainer;
