import React from "react";
import styled, { css } from "styled-components";
import colors from "../config/colors";

const StyledText = styled.p`
  ${({ color }) =>
    color &&
    css`
      color: ${color};
    `}
  ${({ fontSize }) =>
    fontSize &&
    css`
      font-size: ${fontSize};
    `}
  ${({ fontWeight }) =>
    fontWeight &&
    css`
      font-weight: ${fontWeight};
    `}
    ${({ margin }) =>
    margin &&
    css`
      margin: ${margin};
    `}
    ${({ marginLeft }) =>
    marginLeft &&
    css`
      margin-left: ${marginLeft};
    `}
`;

function AppText({
  children,
  color = `${colors.textColor}`,
  fontSize = "14px",
  fontWeight = "400",
  margin = "0",
  marginLeft,
}) {
  return (
    <StyledText
      color={color}
      fontSize={fontSize}
      fontWeight={fontWeight}
      margin={margin}
      marginLeft={marginLeft}
    >
      {children}
    </StyledText>
  );
}

export default AppText;
