import React from "react";

import AppHeading from "./AppHeading";
import AppText from "./AppText";

function ChartWrapper({ children, total, percent }) {
  return (
    <div className="budget-chart__wrapper">
      <AppHeading>This Month</AppHeading>
      <div className="budget-chart__container">
        {children}
        <div className="budget-total d-flex flex-column align-items-center">
          <AppHeading>{total}</AppHeading>
          {percent && (
            <AppText fontSize="10px" fontWeight="bold">
              {percent} spent
            </AppText>
          )}
        </div>
      </div>
      <div className="d-flex justify-content-between"></div>
    </div>
  );
}

export default ChartWrapper;
