import React from "react";
import {
  AiOutlineDollarCircle,
  AiOutlineUser,
  AiOutlineHome,
} from "react-icons/ai";
import SVG from "react-inlinesvg";
import { NavItem, Nav } from "react-bootstrap";
import { Link, useMatch, useResolvedPath } from "react-router-dom";

import "./components.css";
import colors from "../config/colors";
import AppText from "./AppText";

const iconPath = process.env.PUBLIC_URL + "/icons/";

const mobileNavigationContainerStyle = {
  backgroundColor: "white",
};

const mobileNavigationStyle = {
  border: `1px solid ${colors.green}`,
  margin: "2px 10px 0",
  borderRadius: "20px",
  padding: "10px",
};

function MobileNavigationBar(props) {
  function CustomLink({ children, to, ...props }) {
    let resolved = useResolvedPath(to);
    let match = useMatch({ path: resolved.pathname, end: true });

    return (
      <div>
        <Link
          style={{ textDecoration: match ? "underline" : "none" }}
          to={to}
          {...props}
        >
          {children}
        </Link>
      </div>
    );
  }

  return (
    <div style={{}}>
      <nav
        style={mobileNavigationContainerStyle}
        className="navbar navbar-light fixed-bottom"
        role="navigation"
      >
        <Nav style={mobileNavigationStyle} className="w-100">
          <div className=" d-flex flex-row justify-content-around w-100">
            <NavItem>
              <CustomLink as={Link} to="/" className="nav-link p-0">
                <div className="navigation-item">
                  <AiOutlineHome color={colors.textColor} size={24} />
                  <AppText>Home</AppText>
                </div>
              </CustomLink>
            </NavItem>
            <NavItem>
              <CustomLink as={Link} to="/sendmoney" className="nav-link p-0">
                <div className="navigation-item">
                  <AiOutlineDollarCircle color={colors.textColor} size={24} />
                  <AppText>Send Money</AppText>
                </div>
              </CustomLink>
            </NavItem>
            <NavItem>
              <CustomLink as={Link} to="/budget" className="nav-link p-0">
                <div className="navigation-item">
                  <SVG
                    src={`${iconPath}Budget.svg`}
                    color={colors.textColor}
                    height={24}
                    width={24}
                  />
                  <AppText>Budget</AppText>
                </div>
              </CustomLink>
            </NavItem>
            <NavItem>
              <CustomLink as={Link} to="/community" className="nav-link p-0">
                <div className="navigation-item">
                  <SVG
                    src={`${iconPath}Community.svg`}
                    color={colors.textColor}
                    height={24}
                    width={24}
                  />
                  <AppText>Community</AppText>
                </div>
              </CustomLink>
            </NavItem>
            {/* <NavItem>
              <CustomLink as={Link} to="/account" className="nav-link p-0">
                <div className="navigation-item"
                >
                
                  <AiOutlineUser color={colors.textColor} size={24} />
                  <AppText>Account</AppText>
                </div>
              </CustomLink>
            </NavItem> */}
          </div>
        </Nav>
      </nav>
    </div>
  );
}

export default MobileNavigationBar;
