import React from "react";
import { Routes, Route, Link } from "react-router-dom";

import "./App.css";
import HomeScreen from "./screens/HomeScreen";
import SendMoneyScreen from "./screens/SendMoneyScreen";
import CommunityScreen from "./screens/CommunityScreen";
import AccountScreen from "./screens/AccountScreen";
import BudgetReportScreen from "./screens/BudgetReportScreen";
import EditExpensesScreen from "./screens/EditExpensesScreen";
import EditBudgetScreen from "./screens/EditBudgetScreen";

function App() {
  return (
    <>
      <Routes>
        {["/", "/home"].map((path) => (
          <Route key={path} path={path} element={<HomeScreen />} />
        ))}
        <Route path="/sendmoney" element={<SendMoneyScreen />} />
        <Route path="/budget" element={<BudgetReportScreen />} />
        <Route path="/budget/edit-expenses" element={<EditExpensesScreen />} />
        <Route path="/budget/edit-budget" element={<EditBudgetScreen />} />
        <Route path="/community" element={<CommunityScreen />} />
        <Route path="/account" element={<AccountScreen />} />
        <Route path="*" element={<NoMatch />} />
      </Routes>
    </>
  );
}

function NoMatch() {
  return (
    <div>
      <h1>Nothing to see here!</h1>
      <p>
        <Link to="/">Go to the home page</Link>
      </p>
    </div>
  );
}

export default App;
