import React from "react";

import BudgetReportEditItem from "./BudgetReportEditItem";

function BudgetReportEditItemListContainer({ array }) {
  const listItems = array.map((item) => (
    <BudgetReportEditItem
      key={item.label}
      category={item.category}
      label={item.label}
      amount={item.amount}
      color={item.color}
    />
  ));
  return (
    <div>
      <ul style={{ margin: 0, padding: 0 }}>{listItems}</ul>
    </div>
  );
}

export default BudgetReportEditItemListContainer;
